@import url(bootstrap.css);
@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700|Work+Sans:300,400,500,600,700");
html {
  font-family: Work Sans, sans-serif; }

body {
  color: #EDF4ED;
  background: #363636;
  margin: 0; }

h1, h2, h3, h4, h5 {
  font-weight: normal; }

p {
  font-size: 1.3em;
  font-weight: lighter; }

a {
  text-decoration: none;
  color: #FF729F;
  font-weight: initial; }

a:hover {
  background-color: #FF729F;
  color: #363636; }

div.code {
  background: #EDF4ED;
  color: #363636;
  padding: 1em;
  font-family: Inconsolata, monospace;
  counter-reset: line; }
  div.code span {
    width: 100%;
    display: block; }
  div.code span::before {
    counter-increment: line;
    content: counter(line);
    display: inline-block;
    border-right: 1px solid #ddd;
    padding: 0 .5em;
    margin-right: .5em;
    color: #888; }

* {
  box-sizing: border-box; }



