@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700|Work+Sans:300,400,500,600,700");
.landing {
  text-align: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden; }
  .landing .title {
    background: #FF729F;
    padding: 1em 0; }
    .landing .title h1 {
      color: #363636;
      font-size: 4em;
      margin: 0; }
    .landing .title h2 {
      color: #EDF4ED;
      margin: 0; }
  .landing .moffsay {
    margin-top: 10vh;
    display: flex;
    align-items: center;
    justify-content: center; }
    .landing .moffsay img {
      height: 60vh; }
    .landing .moffsay .talk-bubble {
      margin-left: -10vw;
      position: absolute; }

@media only screen and (max-device-width: 600px) {
  .landing .moffsay img {
    width: 80vw;
    height: auto; }
  .landing .moffsay .talk-bubble {
    margin-left: -45vw !important; } }

.moffsay > a:hover {
  animation: random-bg .5s linear infinite; }
  .moffsay > a:hover .talk-bubble {
    animation: grow 1300ms ease infinite; }

@keyframes random-bg {
  from {
    filter: hue-rotate(0); }
  to {
    filter: hue-rotate(360deg); } }

/* pulsating effect on button */
@keyframes grow {
  0% {
    transform: scale(1); }
  14% {
    transform: scale(1.3); }
  28% {
    transform: scale(1); }
  42% {
    transform: scale(1.3); }
  70% {
    transform: scale(1); } }



